import React, {useState, useEffect} from 'react'
import  { Link , useStaticQuery , graphql } from 'gatsby'
import Loadable from '@loadable/component'

const OwlCarousel = Loadable(() => import('react-owl-carousel3'))

const options = {
    loop: true,
    nav: true,
    dots: false,
    autoplayHoverPause: false,
    autoplay: false,
    margin: 30,
    smartSpeed: 1200,
    navText: [
      	'<i class="flaticon-left-arrow"></i>',
      	'<i class="flaticon-right-arrow"></i>'
    ],
    responsive: {
        0: {
            items: 1,
            center: false
        },
        480:{
            items:1,
            center: false
        },
        600: {
            items: 1,
            center: false
        },
        768: {
            items: 2
        },
        992: {
            items: 3
        },
        1200: {
            items: 4
        },
        1280: {
            items: 4
        }
    }
}

const FeaturedProperty = () => {
	const [display, setDisplay] = useState(false);

    useEffect(() => {
        setDisplay(true);
    }, [])
	const data = useStaticQuery(graphql`
	query{
		mouse: file(relativePath: {eq: "resource/mouse.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		fp1: file(relativePath: {eq: "property/fp1.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		fp2: file(relativePath: {eq: "property/fp2.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		fp3: file(relativePath: {eq: "property/fp3.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}	
		pposter1: file(relativePath: {eq: "property/pposter1.png"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		  }
	}`
)
const SliderItem = ({children})=>{
	return(
		<div className="feat_property">
			<Link to="/single/v1">
			<div className="thumb">
				{children}
				<div className="thmb_cntnt">
					<ul className="tag mb0">
						<li className="list-inline-item"><span>For Rent</span></li>
						<li className="list-inline-item"><span>Featured</span></li>
					</ul>
					<ul className="icon mb0">
						<li className="list-inline-item"><span><span className="flaticon-transfer-1" /></span></li>
						<li className="list-inline-item"><span><span className="flaticon-heart" /></span></li>
					</ul>
					<span className="fp_price" >$13,000<small>/mo</small></span>
				</div>
			</div>
			</Link>
			<div className="details">
			<Link to="/single/v1">
				<div className="tc_content">
					<p className="text-thm">Apartment</p>
					<h4>Renovated Apartment</h4>
					<p><span className="flaticon-placeholder" /> 1421 San Pedro St, Los Angeles, CA 90015</p>
					<ul className="prop_details mb0">
						<li className="list-inline-item"><span>Beds: 4</span></li>
						<li className="list-inline-item"><span>Baths: 2</span></li>
						<li className="list-inline-item"><span>Sq Ft: 5280</span></li>
					</ul>
				</div>
				</Link>
				<div className="fp_footer">
					<ul className="fp_meta float-left mb0">
						<li className="list-inline-item"><Link to="/agent/single"><img src={data.pposter1.childImageSharp.fluid.originalImg} alt="pposter1.png" /></Link></li>
						<li className="list-inline-item"><Link to="/agent/single">Ali Tufan</Link></li>
					</ul>
					<div className="fp_pdate float-right">4 years ago</div>
				</div>
			</div>
			
		</div>

							
	)
}
	return (
		<section id="feature-property" className="feature-property mt80 pb50">
		<div className="container-fluid ovh">
			<div className="row">
				<div className="col-lg-12">
					<div className="main-title mb40">
						<h2>Featured Properties</h2>
						<p>Handpicked properties by our team. <a className="float-right" href="#">View All <span className="flaticon-next"></span></a></p>
					</div>
				</div>
					<div className="col-lg-12">
						{display ? <OwlCarousel className="feature_property_home3_slider owl-carousel owl-theme" {...options}> 
                    		<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp3.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp1.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
							<div className="item">
								<SliderItem>
									<img className="img-whp" src={data.fp2.childImageSharp.fluid.originalImg} alt="fp1.jpg" />
								</SliderItem>
							</div>
                		</OwlCarousel> : ''}
					</div>
				</div>
			</div>
		</section>
	)
}

export default FeaturedProperty