import React,{useState} from 'react'
import { Row,Col,Nav,Tab } from 'react-bootstrap'
import AdvanceSearchOption from '../../../Global/App/AdvanceSearchOption/AdvanceSearchOption.component'
import ModalVideo from 'react-modal-video'
import 'react-modal-video/scss/modal-video.scss';
import styled from 'styled-components'
const IconConntainer= styled.div `
    background: #ff5a5f;
    border-radius: 100%;
    font-size: 22px;
    height: 70px;
    left: 0;
    line-height: 70px;
    margin: 0 auto;
    position: absolute;
    right: 0;
    text-align: center;
    top: 43%;
    width: 70px;
    color:#fff;
    &:after{
        background-color: rgba(255, 90, 95, 0.2);
        border-radius: 100%;
        content: "";
        height: 87px;
        left: -7px;
        position: absolute;
        top: -7px;
        width: 87px;
    }
`
const Banner = () => {
    const [show, setShow] = useState(false);
    return (
        <section className="home-three bg-img3">
		<div className="container">
			<div className="row">
				<div className="col-lg-8">
					<div className="home3_home_content">
						<h1>Your Property, Our Priority.</h1>
						<h4>From as low as $10 per day with limited time offer discounts</h4>
					</div>
				</div>
				<div className="col-lg-4">
					<div className="home3_home_content">
                        <ModalVideo channel='youtube' autoplay isOpen={show} videoId="R7xbhKIiw4Y" onClose={() => setShow(false)} />                    
                        <IconConntainer onClick={() => setShow(true)}><i className="flaticon-play"></i></IconConntainer>
					</div>
				</div>
			</div>
            <div className="row">
				<div className="col-lg-12">
                    <div className="home_adv_srch_opt home3">
                        <Tab.Container id="left-tabs-example" defaultActiveKey="buy">
                            <Row>
                                <Col sm={12}>
                                    <Nav id="pills-tab" className="nav nav-pills">
                                        <Nav.Item>
                                            <Nav.Link eventKey="buy">Buy</Nav.Link>
                                        </Nav.Item>
                                        <Nav.Item>
                                            <Nav.Link eventKey="rent">Rent</Nav.Link>
                                        </Nav.Item>
                                    </Nav>
                                </Col>
                                <Col sm={12}>
                                    <Tab.Content id="pills-tabContent" className="home1_adsrchfrm">
                                        <Tab.Pane eventKey="buy">
                                            <AdvanceSearchOption index="1" ClassName="home3" style2="" textthm3 btnthm3 />
                                        </Tab.Pane>
                                        <Tab.Pane eventKey="rent">
                                        <AdvanceSearchOption index="2" ClassName="home3" style2="" textthm3 btnthm3 />
                                        </Tab.Pane>
                                    </Tab.Content>
                                </Col>
                            </Row>
                        </Tab.Container>
                    </div>
                </div>      
            </div>
        </div>
        </section>
    )
}

export default Banner