import React from 'react'
import {graphql, useStaticQuery,Link} from 'gatsby'

const PropertyCitiesHome3 = () => {
	const data = useStaticQuery(graphql`
	query{
		pc1: file(relativePath: {eq: "property/pc7.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc2: file(relativePath: {eq: "property/pc8.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc3: file(relativePath: {eq: "property/pc9.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}
		pc4: file(relativePath: {eq: "property/pc10.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
        }
        pc5: file(relativePath: {eq: "property/pc11.jpg"}) {
			id
			childImageSharp {
			  fluid {
				originalImg
			  }
			}
		}	
	}`
)
	return (
		<section id="property-city" className="property-city pt0 pb30">
		<div className="container-fluid">
			<div className="row">
				  	<div className="col-lg-12">
					<div className="main-title">
						<h2>Find Properties in These Cities</h2>
						<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. <a className="float-right" href="#">View All <span className="flaticon-next"></span></a></p>
					</div>
				    </div>
				</div>
				<div className="row">
	  				<div className="col-sm-6 col-md-4 col-lg-4 col-xl">
					  <Link to="/half-map/v3">
						<div className="properti_city">
						  	<div className="thumb"><img className="img-fluid w100" src={data.pc1.childImageSharp.fluid.originalImg} alt="pc1.jpg" /></div>
						  	<div className="overlay">
								<div className="details">
							  		<h4>Miami</h4>
							  		<p>24 Properties</p>
								</div>
						  	</div>
						</div>
						</Link>
	  				</div>
	  				<div className="col-sm-6 col-md-4 col-lg-4 col-xl">
					<Link to="/half-map/v3">
						<div className="properti_city">
						  	<div className="thumb"><img className="img-fluid w100" src={data.pc2.childImageSharp.fluid.originalImg} alt="pc2.jpg" /></div>
						  	<div className="overlay">
								<div className="details">
								  	<h4>Los Angeles</h4>
								  	<p>18 Properties</p>
								</div>
						  	</div>
						</div>
						</Link>
	  				</div>
	  				<div className="col-sm-6 col-md-4 col-lg-4 col-xl">
					  <Link to="/half-map/v3">
						<div className="properti_city">					
						  	<div className="thumb"><img className="img-fluid w100" src={data.pc3.childImageSharp.fluid.originalImg} alt="pc3.jpg" /></div>
						  	<div className="overlay">
								<div className="details">
							  		<h4>New York</h4>
							  		<p>89 Properties</p>
								</div>
						  	</div>
						</div>						
						</Link>
	  				</div>
	  				<div className="col-sm-6 col-md-4 col-lg-4 col-xl">
					  <Link to="/half-map/v3">
						<div className="properti_city">
						  	<div className="thumb"><img className="img-fluid w100" src={data.pc4.childImageSharp.fluid.originalImg} alt="pc4.jpg" /></div>
						  	<div className="overlay">
								<div className="details">
								  	<h4>Florida</h4>
								  	<p>47 Properties</p>
								</div>
						  	</div>
						</div>
						</Link>
	  				</div>
                      <div className="col-sm-6 col-md-4 col-lg-4 col-xl">
					  <Link to="/half-map/v3">
					<div className="properti_city">
						<div className="thumb"><img className="img-fluid w100" src={data.pc5.childImageSharp.fluid.originalImg} alt="pc11.jpg"/></div>
						<div className="overlay">
							<div className="details">
								<h4>Orlando</h4>
								<p>89 Properties</p>
							</div>
						</div>
					</div>
						</Link>
				</div>
				</div>
  			</div>
		</section>
	)
}

export default PropertyCitiesHome3